
/*
GUIDE USER THROUGH THE PROCESS WITH BOX-SHADOW AND TEXT-SHADOW NEON EFFECTS

 purple: "#BFABFF",
    yellow: "#ebffab",
    orange: "#ffbfab",
    green: "#abffbf",
*/


.App {
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  overflow-x: hidden;
}


#header-container {
  overflow: cover;
}

.input-card {
  text-align: center;
  width: 80vw;
  max-width: 400px;
  padding: 1em;
  padding-bottom: 2em;
  margin: 1em auto;
  border: 2px solid whitesmoke;
  border-radius: 10em / 2em ;
  box-shadow: inset 0 0 4px whitesmoke;
}

.reset-button {
  cursor: pointer;
  margin: 0.5em;
  transition: 200ms;
  background-color: #1a1a1a;
  color: whitesmoke;
  font-size: 1.5em;
  border: 3px solid whitesmoke;
  padding: 0.25em;
  border-radius: 0.35em;
}
.reset-button:hover {
  border: 3px solid #ffbfab;
  box-shadow: 0 0 6px #ffbfab;
}
.submit-button {
  cursor: pointer;
  margin: 0.5em;
  transition: 150ms;
  background-color: #1a1a1a;
  color: whitesmoke;
  font-size: 1.5em;
  border: 3px solid whitesmoke;
  padding: 0.25em;
  border-radius: 0.35em;
  box-shadow: 0 0 6px #abffbf, inset 0 0 6px #abffbf;
  text-shadow: 0 0 3px #abffbf;
}

.submit-button:hover {
  transition: 125ms;
  background-color: #abffbf50;
}

.submit-button:disabled {
  box-shadow: none;
  text-shadow: none;
  color: gray;
  border-color: gray;
}

.submit-button:disabled:hover {
  transition: 100ms;
  box-shadow: 0 0 6px #ffbfab;
  background-color: #1a1a1a;
  border-color: #1a1a1a;
}


#canvas1 {
  /*
  border: 1px solid whitesmoke;
  box-shadow: 0 0 10px #00ffff;
  border-radius: 2rem / 06rem;
  background-color: #00ffff81;
  */
  max-width: 95vw;
  max-height: 17vh;
}
.main {
  margin: 20px auto;
  padding: 1rem;
  padding-bottom: 15em;
  border-radius: 1rem;
  /*
  border: 2px solid gray;
  box-shadow: 0 0 6px #BFABFF, inset 0 0 6px #BFABFF;
  */
  min-height: 70vh;
  height: fit-content;
  width: 90vw;
  background-color: #1a1a1abf;
  box-shadow: 0 0 4px #1a1a1abf;
}

/*  PROGRESS BAR   */

#progress-bar {
  margin-top: 20vh;
  height: fit-content;
  width: 100%;
  display: inline-flex;
  justify-content: center;
}

.progress-block {
  border: 1px solid aqua;
  border-radius: 4px;
  box-shadow: 0 0 5px aqua, inset 0 0 3px #abffbf;
  margin: 2px;
  background-color: #303030;
  transition: 1000ms;
}
.progress-block:hover {
  transition: 200ms;
  background-color: rgba(0, 255, 255, 0.5);
}
.progress-block h4, p {
  margin: 0;
  z-index: 0;
}
.progress-block h3 {
  pointer-events: none;
  margin: 5px;
  z-index: 0;
}

.progress-tooltip {
  position: absolute;
  display: none;
  z-index: 1;
  pointer-events: none;
  top: calc(20vh + 120px);
  width: fit-content;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border: 1px solid whitesmoke;
  padding: 25px 10px 10px;
  border-radius: 4px;
}

.tooltip-legend {
  background-color: #303030;
  padding: 10px;
  border: 2px solid whitesmoke;
  border-radius: 5px;
  box-shadow: 0 0 6px whitesmoke;
  position: absolute;
  transform: translate(0, -60px);
}

/*  CLASS   */

.classSelector {
  transition: 2000ms;
  width: min( max(12vw, 12vh), 120px );
  height: min( max(14vw, 14vh), 140px );
  padding-top: .7em;
  margin: 4px;
  box-shadow: 0 0 5px #BFABFF;
  border: 1px solid whitesmoke;
  border-radius: 3px;
  background-color: whitesmoke;
  color: #1a1a1a;
}
img {
  z-index: 0;
  width: max(6vw, 6vh);
  max-width: 60px;
}
.classSelector h2 {
  font-size: min( max(1.7vw, 1.7vh), 22px );

}


#classSelectors {
  text-align: center;
}
.classSelector:hover {
  box-shadow: inset 0 0 16px #abffbf, 0 0 14px #abffbf;
  border: 2px solid whitesmoke;
  background-color: #fffef5;
}
.classSelector:active {
  box-shadow: 0 0 10px #abffbf, inset 0 0 15px #abffbf;
  background-color: #fffef5;
}

button img, button h2 {
  z-index: 0;
}

#class-button-container {
  width: fit-content;
  max-width: 550px;
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
#level-section {
  visibility: hidden;
  opacity: 0;
}
#levelInput {
  text-align: center;
  font-size: 2em;
  padding: 0.25em;
  max-width: 1.5em;
  border-radius: 0.25em;
  background-color: rgb(70, 70, 70);
  color: whitesmoke;
  border: 2px solid whitesmoke;
  box-shadow: 1px 2px 2px black;
}

#level-input {
  width: fit-content;
  height: fit-content;
  margin: 1em auto;
  display: grid;
  grid-template-columns: repeat(3, 50px);
  row-gap: 0px;
  column-gap: 0px;
  background-color: gray;
  border-radius: 0.5em;
}
#level-input button {
  background-color: #1a1a1a;
  color: whitesmoke;
  border: 2px solid whitesmoke;
  width: 50px;
  height: 50px;
}
#level-input-display {
  background-color: #1a1a1a;
  grid-row: 1/3;
  grid-column: 1/3;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 120%;
  border: 2px solid whitesmoke;
  border-radius: 0.5em 0 0 0.5em;
  border-right-color: transparent;
}

#level-increment:hover {
  box-shadow: 0 0 6px #ebffab;
  text-shadow: 0 0 4px #ebffab;
}
#level-decrement:hover {
  box-shadow: 0 0 6px #ebffab;
  text-shadow: 0 0 4px #ebffab;
}
#level-increment button {
  grid-row: 1/2;
  grid-column: 3/4;
  border-radius: 0 0.2em 0 0;
}
#level-increment i {
  font-size: 2em;
}
#level-decrement i {
  font-size: 2em;
}
#level-decrement button {
  background-color: #1a1a1a;
  color: whitesmoke;
  grid-row: 2/3;
  grid-column: 3/4;
  border: 2px solid whitesmoke;
  border-radius: 0 0 0.2em 0;
}
#continue-button-classlevel {
  transition: 200ms;
  background-color: #1a1a1a;
  color: whitesmoke;
  font-size: 1.5em;
  border: 3px solid whitesmoke;
  padding: 0.25em;
  border-radius: 0.35em;
}
#continue-button-classlevel:hover {
  background-color: #abffbf30;
  transition: 200ms;
  transform: scale(1.1);
  box-shadow: 0 0 7px #abffbf;
}
#display-class-level {
  text-shadow: 0 0 6px #BFABFF;
  width: fit-content;
  position: relative;
  top: 0;
  left: 3rem;
}
/*  RACE  */

#race-container {
  text-align: center;
  padding: 1em;
  margin: 3rem auto;
  border: 3px solid whitesmoke;
  border-radius: 10em / 2em ;
  box-shadow: 0 0 6px whitesmoke;
  transition: 1000ms;
}
.race-button {
  transition: 600ms ease-out;
  font-size: 1.25em;
  width: 6em;
  padding: 0.25em;
  margin-top: 0.5em;
  border-radius: 0.25em;
  background-color: #1a1a1a;
  color: whitesmoke;
  border: 2px solid whitesmoke;
  box-shadow: 0 0 6px whitesmoke;
}
.race-button:hover {
  transition: 50ms;
  box-shadow: inset 0 0 7px whitesmoke;
  color: #abffbfbd;
}
.subRace {
  margin: 0.25em 0.5em 0.5em
}

/*  ABILITY SCORES  */

#enter-rolls-display input {
  height: 2rem;
  width: 4rem;
}
#submitScore {
  height: 2rem;
  width: 4rem;
}
#standard-array {
  display: block;
  margin: 1rem auto;
}

.ability-score-increase button {
  width: 7em;
  height: 2em;
  margin: 2px;
  border: 1px solid whitesmoke;
  border-radius: 4px;
  box-shadow: 0 0 6px #BFABFF, inset 0 0 6px #BFABFF;
}
.prompt-AS-increase button {
  transition: 100ms;
  font-size: min(3vw, 3vh);
  padding: 4px 12px;
  border-radius: 7px;
  border: 2px solid whitesmoke;
  box-shadow: 0 0 6px #abffbf, inset  0 0 6px #abffbf;
  background-color: #1a1a1a;
  color: whitesmoke;
  text-shadow: 0 0 3px #abffbf;
  
}
.as-increase-container {
  padding: 1rem;
}
.prompt-AS-increase button:hover {
  transition: 150ms;
  transform: scale(1.05);
  background-color: #abffbf7e;
}

.ability-score-increase {
}

.ability-score-increase button:hover {
  box-shadow: 0 0 6px #ebffab, inset 0 0 6px #ebffab;
}


.ability-scores-container{
  height: fit-content;
}

#score-cards-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.rollCard {
  cursor: pointer;
  transition-delay: 150ms;
  transition: 200ms;
  font-size: min( 2.7vh, 2.7vh);
  text-shadow: 0 0 6px #abffbf;
  width: min(12vw, 4em);
  height: min(12vw, 4em);
  margin: max(10px, 2vw);
  display: inline-flex;
  border: 1px solid whitesmoke;
  border-radius: min(1vw, 1.25em);
  box-shadow: 0 0 6px #abffbf, inset 0 0 6px #abffbf;
}

.rollCard h2 {
  margin: auto;
  pointer-events: none;
}

.rollCard:hover {
  transition: 150ms;
  border-color: #abffbf;
  transform: scale(1.05);
  box-shadow: inset 0 0 12px #abffbf;
}

.AS-bonus {
  width: 50px;
  height: 50px;
  position: absolute;
  font-size: 25px;
  text-align: center;
  transition-delay: 400ms;
  transition-duration: 300ms;
  text-shadow: 0 0 6px #abffbf;
  color: #abffbf;
}

#prompt-stat {
  width: 100%;
  text-align: center;
}

.fancy-AS-display {
}


#displayBaseStats {
  width: fit-content;
  margin: auto;
  text-align: center;
}
#base-stats-submitted {
}
#displayBaseStats-alt {
  transform: scale(0.55) translate( 0, -50% );
  pointer-events: none;
  font-size: 25px;
}

#display {
}

#displayBaseStats text {
  font-size: 1.25em;
}
#submit-as-button {
  border: 2px solid #abffbf;
  box-shadow: 0 0 3px #abffbf;
}
#score-cards-container {
  width: fit-content;
  margin: auto;
}

/*   BACKGROUND   */
#background-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
#background-container h2 {
  grid-column: 1 / 3;
}
#alignment-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
#alignment-container h2 {
  grid-column: 1 / 3;
}

/*  SKILLS  */

.skill-option-container, .language-option-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 0.25rem;
  column-gap: 0.25rem;
}

/*  EQUIPMENT  */

.equipment-options {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 1rem;
}
.EOcontainer {
  display: grid;
  grid-template-columns: 1fr 0.25fr 1fr;
}


